/** @jsx jsx */
import React from 'react'
import { jsx, css } from '@emotion/core'
import { Container, Typography, Row, Col } from 'reus'
import styled from '@emotion/styled'
import { StaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'

const Service = () => {
  const FeatureWrapped = styled.div`
    label: feature;
    padding: 70px 0 30px 0px;
  `
  const HeadWrapper = styled.div`
    margin-bottom: 70px;
  `
  return (
    <StaticQuery
      query={graphql`
        query ServiceQuery {
          attendance: file(relativePath: { eq: "attendance.png" }) {
            childImageSharp {
              fixed(width: 80) {
                ...GatsbyImageSharpFixed
              }
            }
          }
        }
      `}
      render={data => {
        return (
          <FeatureWrapped>
              <Container md gutter={22}>
                <HeadWrapper>
                  <Typography
                    type="headline"
                    align="center"
                    css={theme => css`color: ${theme.color.secondary};`}
                    tag="h2">
                    Services
                  </Typography>
                  <Typography
                    type="intro"
                    align="center"
                    tag="p">
                    The services that help your company start from Managing your employee information, Attendance, Payroll disbursement, and Employee lending.
                  </Typography>
                </HeadWrapper>
              </Container>
          </FeatureWrapped>
        )
      }}
    />
    
  )
}

export default Service